p, h1, h2 {
  margin: 0;
}

.is-hidden {
  width: 1px;
  height: 1px;
  white-space: nowrap;
  -webkit-clip-path: inset(100%);
  clip-path: inset(100%);
  clip: rect(0 0 0 0);
  border: 0;
  margin: -1px;
  padding: 0;
  position: absolute;
  overflow: hidden;
}

.loader {
  width: 84px;
  height: 84px;
  background: #fff;
  border-radius: 4px;
  position: relative;
  overflow: hidden;
}

.loader:before {
  content: "";
  width: 40px;
  height: 40px;
  background: #ff9371;
  animation: slide 2s ease-in-out infinite alternate;
  position: absolute;
  bottom: 0;
  left: 0;
  transform: rotate(45deg)translate(30%, 40%);
  box-shadow: 32px -34px 0 5px #ff3d00;
}

.loader:after {
  content: "";
  width: 16px;
  height: 16px;
  transform-origin: 35px 145px;
  background: #ff3d00;
  border-radius: 50%;
  animation: rotate 2s ease-in-out infinite;
  position: absolute;
  top: 10px;
  left: 10px;
  transform: rotate(0deg);
}

@keyframes slide {
  0%, 100% {
    bottom: -35px;
  }

  25%, 75% {
    bottom: -2px;
  }

  20%, 80% {
    bottom: 2px;
  }
}

@keyframes rotate {
  0% {
    transform: rotate(-15deg);
  }

  25%, 75% {
    transform: rotate(0deg);
  }

  100% {
    transform: rotate(25deg);
  }
}

.cat-info {
  gap: 24px;
  padding: 24px 0;
  display: flex;
}

.text-box {
  width: 600px;
}

.img-box {
  max-width: 100%;
  height: auto;
  display: block;
}

.heard-text {
  margin-bottom: 24px;
}

.description {
  margin-bottom: 16px;
}

